<template>
  <span>
    <b-card no-body>
      <b-card-header>
        <b-row>
          <b-col md="9">
            <h3 tabindex="0"><strong>Gerenciar Perfis</strong></h3>
          </b-col>
          <b-col md="3">
            <b-button @click="navigate('/perfil/cadastrar')" block variant="primary">
              <i class="fa fa-plus"></i>
              <span>Novo Perfil</span>
            </b-button>
          </b-col>
        </b-row>
      </b-card-header>
      <b-card-body>
        <b-row>
          <b-col sm="8">
            <b-form-group tabindex="0" label="Pesquisar por nome do perfil: ">
              <b-form-input type="text" id="name" v-model="filtros.filter" placeholder="" v-on:keyup.enter="pesquisar">
              </b-form-input>
            </b-form-group>
          </b-col>
          <b-col sm="4">
            <b-form-group tabindex="0" label="Situação:">
              <b-form-select v-model="filtros.ativo" class="mb-3">
                <option selected :value="null"> Selecione </option>
                <option v-for="situacao in situacoes" :value="situacao.value" :key="situacao.value"> {{ situacao.text }}
                </option>
              </b-form-select>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col sm="2">
            <b-button block @click="limpar">
              <i class="fa fa-eraser"></i>&nbsp;&nbsp;<span>Limpar filtros</span>
            </b-button>
          </b-col>
          <b-col sm="2">
            <b-button block variant="primary" @click="pesquisar">
              <i class="fa fa-search"></i>&nbsp;&nbsp;<span>Pesquisar</span>
            </b-button>
          </b-col>
        </b-row>
      </b-card-body>
      <b-row>
        <b-col sm="12">
          <b-container fluid>
            <span>
              <table aria-rowcount="4" id="my-table" aria-busy="false" aria-colcount="7"
                class="table table-responsive-sm table-bordered table-striped table-sm">
                <thead>
                  <tr>
                    <th tabindex="0" scope="col">Nome do Perfil</th>
                    <th tabindex="0" scope="col">Descrição</th>
                    <th tabindex="0" scope="col">Situação</th>
                    <th tabindex="0" scope="col">Ações</th>
                  </tr>
                </thead>

                <tbody v-if="perfis.data !== undefined">
                  <tr class="action" v-for="item in perfis.data" :key="item.id">
                    <td tabindex="0">{{ item.nome }}</td>
                    <td tabindex="0">{{ item.descricao == null ? '-' : item.descricao }}</td>
                    <td tabindex="0">{{ item.ativo == false ? 'Inativo' : 'Ativo' }}</td>
                    <td style="width: 1%; white-space: nowrap;">
                      <button v-if="!item.fixo" class="btn btn-sm btn-secondary action" title="Editar"
                        @click="editar(item.codigo)">
                        <i class="fa fa-edit"></i>
                      </button>
                      <button class="btn btn-sm btn-secondary action" title="Visualizar"
                        @click="visualizar(item.codigo)">
                        <i class="fa fa-search"></i>
                      </button>
                      <button v-show="item.ativo == 1" class="btn btn-sm btn-secondary action" title="Desativar"
                        @click="alterarSituacao(item.id, 0)">
                        <i class="fa fa-toggle-off"></i>
                      </button>
                      <button v-show="item.ativo == 0" class="btn btn-sm btn-secondary action" title="Ativar"
                        @click="alterarSituacao(item.id, 1)">
                        <i class="fa fa-toggle-on"></i>
                      </button>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr class="action">
                    <td tabindex="0" colspan="7">Nenhum perfil encontrado</td>
                  </tr>
                </tbody>
              </table>
              <b-row>
                <b-col cols="4">
                  <pagination tabindex="0" v-show="perfis.data !== undefined" :totalPorPagina="totalPorPagina"
                    :resource="resource" :filtros="filtros"></pagination>
                </b-col>
              </b-row>
            </span>
          </b-container>
        </b-col>
      </b-row>
    </b-card>
  </span>
</template>

<script>
import Vue from 'vue'
import Pagination from '@/components/shared/Pagination'
import { eventType } from '@/components/shared/eventType'
import 'v-calendar/lib/v-calendar.min.css'
import miniToastr from 'mini-toastr'

miniToastr.init()

export default {
  components: {
    Pagination
  },
  created () {
    /* if (!this.can('perfil.index')) {
      this.$router.push('/home')
    } */
  },
  data () {
    return {
      totalPorPagina: 10,
      resource: 'perfil',
      filtros: {
        filter: '',
        ativo: null
      },
      situacoes: [
        { value: true, text: 'Ativo' },
        { value: false, text: 'Inativo' }
      ]
    }
  },
  computed: {
    perfis () {
      if (this.$store.state.pagination.getList.data !== undefined) {
        if (this.$store.state.pagination.getList.data.length !== 0) {
          return this.$store.state.pagination.getList
        } else {
          return false
        }
      }
      return this.$store.state.pagination.getList
    }
  },
  methods: {
    can (permissao) {
      return this.$store.getters.validaPermissao(permissao)
    },
    setarFiltros (filtros) {
      eventType.$emit('app', filtros)
    },
    pesquisar () {
      this.$store.dispatch('getRegistrosSearch', { resource: this.resource, limit: this.totalPorPagina, filtros: this.filtros })
      this.setarFiltros(this.filtros)
    },
    navigate (route) {
      this.$router.push(route)
    },
    alterarSituacao (id, situacao) {
      var msg, path
      if (situacao > 0) {
        msg = 'Deseja ativar perfil selecionado?'
        path = 'active/' + id
      } else {
        msg = 'Deseja desativar perfil selecionado?'
        path = 'inactive/' + id
      }

      this.$bvModal.msgBoxConfirm(msg, {
        okTitle: 'Confirmar',
        cancelTitle: 'Cancelar'
      }).then(valor => {
        if (valor) {
          Vue.http.post('api/perfil/' + path).then(response => {
            if (response.body.success) {
              this.sucesso(response.body.msg)
              this.$store.dispatch('getRegistrosSearch', { resource: this.resource, limit: this.totalPorPagina, filtros: this.filtros })
              this.setarFiltros(this.filtros)
            } else {
              this.erro('Erro ao alterar situação!')
            }
            this.loading = false
          }).catch(e => {
            console.log('Servidor está fora! ' + e)
          })
        }
      })
    },
    limpar () {
      this.filtros.nome_perfil = null
      this.filtros.status = null

      this.pesquisar()
    },
    editar (id) {
      this.$router.push('/perfil/' + id + '/editar')
    },
    visualizar (id) {
      this.$router.push('/perfil/' + id + '/visualizar')
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css">
</style>
<style>
.multiselect.invalid .multiselect__tags,
.multiselect.invalid .multiselect__tags span,
.multiselect.invalid .multiselect__tags input {
  background: red;
}

td {
  max-width: 50ch;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



