import Vue from 'vue'

const state = {
  listaperfilUser: []
}

const mutations = {
  setperfilUser (state, data) {
    state.listaperfilUser = data
  }
}

const getters = {
}

const actions = {
  getPerfilUnidades (context, id) {
    Vue.http.get('api/unidades/perfilUser/' + id).then(response => {
      context.commit('setperfilUser', response.body.data)
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



