import SessionStorage from '@/services/session-storage'
import { Jwt } from '@/services/resources'
export default {
  get token () {
    return SessionStorage.get('token')
  },
  set token (value) {
    SessionStorage.set('token', value)
  },
  get nome () {
    return SessionStorage.get('nome')
  },
  set nome (value) {
    SessionStorage.set('nome', value)
  },
  get usuario () {
    return SessionStorage.get('usuario')
  },
  set usuario (value) {
    SessionStorage.set('usuario', value)
  },
  accessToken (data) {
    this.token = data.authorisation.token
    this.nome = data.user.nome
    this.usuario = data.user

    if (data.permissoes && data.permissoes.length > 0) {
      this.permissoes = data.permissoes
    }
  },
  refreshToken () {
    return Jwt.refreshToken().then(response => {
      this.token = response.data.authorisation.token
      return response
    }).catch(() => {
      return false
    })
  },
  getAuthorizationHeader () {
    return `Bearer ${this.token}`
  }
}



