                    />
                  </b-input-group>
                </b-form-group>
              </b-col>
              </b-row>
             <b-row>
                  <b-col cols="5">
                <b-form-group label="DDD + Telefone do Órgão:" :state="telefoneOrgaoState" invalid-feedback="Telefone do Órgão é obrigatório.">
                  <b-input-group class="mb-3">
                    <b-input-group-prepend>
                      <b-input-group-text><i class="fa fa-phone"></i></b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input type="text"
                                  v-mask="'+55 (##) #####-####'"
                                  class="form-control"
                                  placeholder="Telefone"
                                  autocomplete="telefone"
                                  :state="telefoneOrgaoState"
                                  v-model="telefone"
                    />
                  </b-input-group>
                </b-form-group>
              </b-col>
                  <b-col cols="7">
                <b-form-group label="Responsável pelo Órgão:" :state="responsavelState" invalid-feedback="Responsável do Órgão é obrigatório.">
                  <b-input-group class="mb-3">
                    <b-form-input type="text"
