                    />
                  </b-input-group>
                </b-form-group>
              </b-col>
              </b-row>

             <br>
             <b-row>
                <b-col cols="12">
                <b-form-group label="Aceite para o termo de uso de dados para o SINAJUVE">
                  <b-input-group class="mb-3">
                    <b-form-input type="text"
                                  class="form-control"
                                  v-model="usuario.termo_sinajuve"
                                  :disabled="true"/>
                  </b-input-group>
                </b-form-group>
              </b-col>
             </b-row>

            <b-row v-if="rg">
               <b-col cols="5">
                 <b-button variant="primary" @click="abrirDocumento(rg)"><i class="fa fa-download"></i> Visualizar Identidade (RG)</b-button>
               </b-col>
             </b-row>
             <br>
             <b-row v-if="cpf">
               <b-col cols="5">
                 <b-button variant="primary" @click="abrirDocumento(cpf)"><i class="fa fa-download"></i> Visualizar CPF</b-button>
               </b-col>
             </b-row>
             <br>
             <b-row v-if="cv">
               <b-col cols="5">
                 <b-button variant="primary" @click="abrirDocumento(cv)"><i class="fa fa-download"></i> Visualizar Mini Currículo</b-button>
               </b-col>
             </b-row>
             <br>
             <b-row v-if="ato">
               <b-col cols="5">
                 <b-button variant="primary" @click="abrirDocumento(ato)"><i class="fa fa-download"></i> Visualizar Ato de Nomeação</b-button>
               </b-col>
             </b-row>
             <br>
             <b-row v-for="(rede, index) in usuario.redes" :key="`B-${index}`">
                        <b-col cols="12" md="8">
                            <b-form-group label="Redes Sociais Pessoais e do Órgão:" label-for="nome-input" :state="redeState" invalid-feedback="Informe ao menos uma rede social.">
                                    <b-input-group class="mb-6">
                                        <b-input-group-prepend>
                                            <b-input-group-text><i class="icon-user"></i></b-input-group-text>
                                        </b-input-group-prepend>
                                        <b-form-input
                                          type="text"
                                          class="form-control"
                                          placeholder="Link da rede social"
                                          autocomplete="nome"
                                          v-model="rede.link"
                                          id="nome-input"
                                          :state="redeState"
                                          />
                                        </b-input-group>
                                </b-form-group>
                        </b-col>
                        <b-col cols="12" md="2">
                           <label>&nbsp;</label>
                           <b-col lg="8" class="pb-8">
                               <b-button variant="primary" size="sm" v-on:click="adicionarRede"><i class="fa fa-plus"></i></b-button>&nbsp;
                               <b-button variant="secondary" size="sm" v-on:click="removerRede(index)"><i class="fa fa-times"></i></b-button></b-col>
                        </b-col>
              </b-row>
          </b-form>
        </b-card-body>
          <b-card-footer class="p-12">
            <b-button variant="primary" @click="onSubmit"><i class="fa fa-check"></i> Salvar</b-button>
            <b-button variant="primary" @click="modalAlterarSenha = true"><i class="fa fa-key"></i> Alterar Senha</b-button>
            <b-button variant="secondary" @click="$router.go(-1)"><i class="fa fa-mail-reply-all"></i> Voltar</b-button>
        </b-card-footer>
       </b-card>
        <b-modal id="modal-prevent-closing" ref="modal" title="Alterar Senha" ok-title="Salvar" cancel-title="Voltar" v-model="modalAlterarSenha" @show="limparModal" @hidden="limparModal" @ok="handleOk">
            <form ref="form" @submit.stop.prevent="alterarSenha">
              <b-form-group :state="senhaAtualState" label="Senha Atual:" label-for="senha-atual-input" invalid-feedback="O campo Senha Atual é obrigatório!">
                <b-form-input id="senha-atual-input" type="password" v-model="senhaAtual" :state="senhaAtualState" required></b-form-input>
              </b-form-group>
              <b-form-group :state="senhaNovaState" label="Nova Senha:" label-for="nova-senha-input" invalid-feedback="O campo para Nova Senha é obrigatório!">
                <b-form-input id="nova-senha-input" type="password" v-model="senhaNova" :state="senhaNovaState" required></b-form-input>
              </b-form-group>
              <b-form-group :state="senhaNovaConfirmaState" label="Confirma a Senha:" label-for="confirma-senha-input" invalid-feedback="O campo para Confirmação de senha é obrigatório!">
                <b-form-input id="confirma-senha-input" type="password" v-model="senhaNovaConfirma" :state="senhaNovaConfirmaState" required></b-form-input>
              </b-form-group>
            </form>
        </b-modal>
    </span>
</template>

<script>
import Vue from 'vue'
import miniToastr from 'mini-toastr'
import SessionStorage from '@/services/session-storage'

miniToastr.init()

export default {
  created () {
    this.$store.dispatch('buscarEstados')
    this.$store.dispatch('buscarTipoUsuarios')
    this.$store.dispatch('buscarCidades')
    this.$store.dispatch('getUsuario', SessionStorage.get('usuario'))
  },
  data () {
    return {
      mostrarEstados: false,
      mostrar_cidades: false,
      mostrar_estados: false,
      uf: null,
      valueAceiteTipoUsuario: false,
      options: [
        {text: 'Feminino', value: 'F'},
        {text: 'Masculino', value: 'M'}
      ],
      senhaAtual: '',
      senhaNova: '',
      senhaNovaConfirma: '',
      senhaAtualState: null,
      senhaNovaState: null,
      senhaNovaConfirmaState: null,
      submittedNames: [],
      modalAlterarSenha: false,
      mensagem_alteracao_basica: false,
      mensagem_alteracao_completa: false,
      rede: {
        link: ''
      },
      redes: [],
      orgao: '',
      cargo: '',
      endereco: '',
      estado_id: null,
      telefone: '',
      responsavel: '',
      ato: '',
      cpf: '',
      cv: '',
      rg: '',
      dataState: null,
      nomeState: null,
      telefoneState: null,
      ufState: null,
      municipioState: null,
      emailState: null,
      orgaoState: null,
      cargoState: null,
      enderecoState: null,
      telefoneOrgaoState: null,
      responsavelState: null,
      rgState: null,
      cpfState: null,
      atoState: null,
      cvState: null,
      redeState: null
    }
  },
  computed: {
    estados () {
      return this.$store.state.estado.listaEstados
    },
    cidades () {
      return this.$store.state.cidade.listaCidades
    },
    listacombo () {
      return this.$store.getters.getListaCombo
    },
    usuario () {
      return this.$store.state.usuario.usuarioEditar
    },
    perfilAtual () {
      return this.$store.state.usuario.perfilAtual
    }
  },
  watch: {
    'usuario.estado_id': function () {
      if (this.usuario.estado_id !== null) {
        this.$store.dispatch('buscarCidadesPorEstado', this.usuario.estado_id)
      }
    },
    'usuario.perfil_id': function () {
      if (this.usuario.perfil_id === 3) {
        this.mostrar_cidades = false
        this.mostrar_estados = true
      } else {
        this.mostrar_cidades = true
        this.mostrar_estados = false
        this.uf = this.usuario.cidades[0].cidade.estado.id
      }
    },
    'usuario.redes': function () {
      this.redes = this.usuario.redes
    },
    'usuario.entidade': function () {
      this.orgao = this.usuario.entidade.orgao
      this.cargo = this.usuario.entidade.cargo
      this.endereco = this.usuario.entidade.endereco
      this.telefone = this.usuario.entidade.telefone_orgao
      this.responsavel = this.usuario.entidade.responsavel

      for (let i = 0; i < this.usuario.documentos.length; i++) {
        if (this.usuario.documentos[i].tipo === '0') {
          this.ato = this.usuario.documentos[i].documento
        }

        if (this.usuario.documentos[i].tipo === '1') {
          this.cpf = this.usuario.documentos[i].documento
        }

        if (this.usuario.documentos[i].tipo === '2') {
          this.cv = this.usuario.documentos[i].documento
        }

        if (this.usuario.documentos[i].tipo === '3') {
          this.rg = this.usuario.documentos[i].documento
        }
      }
    }
  },
  methods: {
    getStatus () {
      if (this.usuario.situacao === 0) { return 'Pendente' }
      if (this.usuario.situacao === 1) { return 'Aprovado' }
      if (this.usuario.situacao === 2) { return 'Rejeitado' }
      if (this.usuario.situacao === 3) { return 'Aguardando Correção' }
      if (this.usuario.situacao === 4) { return 'Corrigido' }
    },
    salvar () {
      let data
      let bool = 'simples' // mensagem simples

      data = {
        cpf: this.usuario.cpf.replace(/[^\d]+/g, ''),
        data_nascimento: this.usuario.data_nascimento,
        sexo: this.usuario.sexo,
        foto: this.usuario.foto,
        nome: this.usuario.nome,
        telefone: this.usuario.telefone.replace(/[^\d]+/g, ''),
        orgao: this.orgao,
        cargo: this.cargo,
        endereco: this.endereco,
        responsavel: this.responsavel,
        telefone_orgao: this.telefone.replace(/[^\d]+/g, ''),
        estado_id: parseInt(this.uf),
        cidade_id: parseInt(this.usuario.cidade_id),
        email: this.usuario.email,
        email_confirmation: this.usuario.email,
        ativo: true,
        doc_ato: this.ato,
        doc_cpf: this.cpf,
        doc_rg: this.rg,
        doc_cv: this.cv,
        redes: this.redes
      }

      Vue.http.put('api/usuario/update/' + SessionStorage.get('usuario'), data).then(response => {
        if (response.body.success) {
          if (bool === 'basica') {
            this.mensagem_alteracao_basica = true
            this.mensagem_alteracao_completa = false
            setTimeout(() => this.$router.push({path: '/'}), 10000)
          } else if (bool === 'completa') {
            this.mensagem_alteracao_basica = false
            this.mensagem_alteracao_completa = true
            setTimeout(() => this.$router.push({path: '/'}), 10000)
          } else {
            miniToastr.setIcon('success', 'i', {'class': 'fa fa-check'})
            miniToastr.success(response.body.msg, 'Sucesso!', 4500)
            this.$router.push({path: '/'})
          }
          window.scrollTo({ top: 0, behavior: 'smooth' })
        } else {
          miniToastr.setIcon('error', 'i', {'class': 'fa fa-warning'})
          miniToastr.error(response.body.msg, 'Alerta', 3500)
        }
      }).catch(e => {
        console.log('Servidor está fora! ' + e)
      })
    },
    onSubmit () {
      if (this.validate()) {
        this.confirmar()
      }
    },
    alterarSenha () {
      this.modalAlterarSenha = true
    },
    validate () {
      let result = true

      if (!this.usuario.data_nascimento) {
        this.dataState = false
        result = false
      } else {
        this.dataState = true
      }

      if (!this.usuario.nome) {
        result = false
        this.nomeState = false
      } else {
        this.nomeState = true
      }

      if (!this.usuario.sexo) {
        result = false
      }

      if (!this.usuario.telefone) {
        result = false
        this.telefoneState = false
      } else if (this.usuario.telefone.length < 19) {
        result = false
        this.telefoneState = false
      } else {
        this.telefoneState = true
      }

      if (!this.usuario.email) {
        result = false
        this.emailState = false
      } else if (!this.validateEmail(this.usuario.email)) {
        result = false
        this.emailState = false
      } else {
        this.emailState = true
      }

      if (!this.orgao) {
        result = false
        this.orgaoState = false
      } else {
        this.orgaoState = true
      }

      if (!this.cargo) {
        result = false
        this.cargoState = false
      } else {
        this.cargoState = true
      }

      if (!this.endereco) {
        result = false
        this.enderecoState = false
      } else {
        this.enderecoState = true
      }

      if (!this.usuario.telefone) {
        result = false
        this.telefoneState = false
      } else {
        this.telefoneState = true
      }

      if (!this.telefone) {
        result = false
        this.telefoneOrgaoState = false
      } else {
        this.telefoneOrgaoState = true
      }

      if (!this.responsavel) {
        result = false
        this.responsavelState = false
      } else {
        this.responsavelState = true
      }

      if (this.redes[0].link === '') {
        result = false
        this.redeState = false
      } else {
        this.redeState = true
      }

      return result
    },
    confirmar () {
      this.$bvModal.msgBoxConfirm('Tem certeza que deseja continuar?', {
        okTitle: 'Confirmar',
        cancelTitle: 'Cancelar'
      }).then(valor => {
        if (valor) {
          this.salvar()
        }
      }).catch(erro => {
        console.log(erro)
      })
    },
    validateEmail: function (email) {
      let re = /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return re.test(email)
    },
    checkFormValidity () {
      const valid = this.$refs.form.checkValidity()
      this.senhaAtualState = !!this.senhaAtual
      this.senhaNovaState = !!this.senhaNova
      this.senhaNovaConfirmaState = !!this.senhaNovaConfirma
      return valid
    },
    limparModal () {
      this.senhaAtual = ''
      this.senhaNova = ''
      this.senhaNovaConfirma = ''
      this.senhaAtualState = null
      this.senhaNovaState = null
      this.senhaNovaConfirmaState = null
    },
    handleOk (bvModalEvt) {
      bvModalEvt.preventDefault()
      this.handleSubmit()
    },
    handleSubmit () {
      if (!this.checkFormValidity()) {
        return
      }

      let data

      data = {
        old_password: this.senhaAtual,
        new_password: this.senhaNova,
        new_password_confirmation: this.senhaNovaConfirma
      }

      Vue.http.post('api/usuario/alterarMinhaSenha', data).then(response => {
        if (response.body.success) {
          this.sucesso(response.body.msg)
          setTimeout(() => this.$router.push({path: '/'}), 3500)
        } else {
          this.erro(response.body.msg)
        }
        this.$nextTick(() => {
          this.$refs.modal.hide()
        })
      }).catch(e => {
        let response = e.body

        if (response.msg) {
          this.erro(response.msg)
        }

        if (response.errors) {
          let erros = response.errors

          if (erros.old_password !== undefined) {
            for (let erro of erros.old_password) {
              this.erro(erro)
            }
          }

          if (erros.new_password !== undefined) {
            for (let erro of erros.new_password) {
             this.erro(erro)
            }
          }

          if (erros.new_password_confirmation !== undefined) {
            for (let erro of erros.new_password_confirmation) {
              this.erro(erro)
            }
          }
        }

        this.limparModal()
      })
    },
    adicionarRede: function () {
      this.usuario.redes.push(Vue.util.extend({}, this.rede))
    },
    removerRede: function (index) {
      if (this.usuario.redes.length > 1) {
        Vue.delete(this.usuario.redes, index)
      }
    },
    abrirDocumento (item) {
      window.open(item, '_blank')
    },
    uploadFoto (evento) {
      let arquivo = evento.target.files || evento.dataTransfer.files

      if (!arquivo.length) {
        return false
      }

      let reader = new FileReader()

      reader.onload = (evento) => {
        this.usuario.foto = evento.target.result
      }
      reader.readAsDataURL(arquivo[0])
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      console.log(mensagem)
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    },
  }
}
</script>

<style>

  .fileInput {
    word-break: break-all;
    overflow: hidden;
  }

</style>



