res, restaurantes e lojas em geral.\n              Em 2012 foi publicada a Lei nº 12.764, de 27 de dezembro de 2012, que instituiu a Política Nacional de Proteção dos Direitos da Pessoa com Transtorno do Espectro Autista. Essa lei foi um grande marco para pessoas com TEA e seus familiares.\n              Já em 2020, essa lei foi alterada pela Lei nº 13.977, de 8 de janeiro de 2020, que instituiu a Carteira de Identificação da Pessoa com Transtorno do Espectro Autista (Ciptea) com o objetivo de garantir atenção integral, pronto atendimento e prioridade no atendimento e no acesso aos serviços públicos e privados, em especial nas áreas de saúde, educação e assistência social para a pessoa com Transtorno do Espectro Autista.\n              A Lei estabelece que a CIPTEA deve ser expedida pelos Estados, Distrito Federal e Municípios, e estabelece a necessidade de providenciar a contagem das pessoas com transtorno do espectro autista em todo o território nacional. Assim, a Secretaria Nacional dos Diretos da Pessoa com Deficiência lança o Sistema Nacional de emissão da Carteira de Identificação da Pessoa com Transtorno do Espectro Autista.\n              Vale destacar que a emissão da referida carteirinha nacional não substitui a avaliação da deficiência, prevista na Lei Brasileira de Inclusão da Pessoa com Deficiência (Lei nº 13.146, de 6 de julho de 2015), artigo 2º, § 1º. Entretanto, espera-se que a emissão nacional e padronizada da CIPTEA contribua para a criação de um painel de Business Intelligence que contribuirá com o monitoramento e melhoria das políticas públicas votadas para as pessoas com TEA.\n            ")])])],1)],1)],1),_vm._v(" "),_c('b-card-footer',{staticStyle:{"text-align":"center"}},[_c('row',[_c('b-button',{attrs:{"variant":"info","size":"lg"},on:{"click":function($event){return _vm.navigate('/carteirinha/gerar')}}},[_vm._v("Emitir Carteirinha")]),_vm._v(" "),_c('b-button',{attrs:{"variant":"warning","size":"lg"},on:{"click":function($event){return _v