/* eslint-disable eqeqeq */

<template>
  <div class="app">
    <AppHeader fixed>
      <SidebarToggler class="d-lg-none" display="md" mobile />
      <b-link class="navbar-brand" to="/home">
        <h6 class="navbar-brand-full" tabindex="0" width="50" height="25" style="color: black; font-weight: bold;" alt="Chama">Chama Family</h6>
      </b-link>
      <SidebarToggler class="d-md-down-none" display="lg" />
      <b-navbar-nav class="ml-auto">
        <b-nav-item class="d-md-down-none">
        </b-nav-item>
        <DefaultHeaderDropdownAccnt/>
      </b-navbar-nav>
    </AppHeader>
    <div class="app-body">
      <AppSidebar fixed>
        <SidebarHeader/>
        <SidebarForm/>
        <SidebarNav :navItems="nav"></SidebarNav>
        <SidebarFooter/>
        <SidebarMinimizer/>
      </AppSidebar>
      <main class="main">
        <Breadcrumb :list="list"/>
        <div class="container-fluid">
          <router-view></router-view>
        </div>
      </main>
    </div>
    <TheFooter>
      <!--footer-->
      <div>
        <span class="ml-1">ChamaTech &copy; 2022</span>
      </div>
    </TheFooter>
  </div>
</template>

<script>
// import Vue from 'vue'
import { Header as AppHeader, SidebarToggler, Sidebar as AppSidebar, SidebarFooter, SidebarForm, SidebarHeader, SidebarMinimizer, SidebarNav, Aside as AppAside, AsideToggler, Footer as TheFooter, Breadcrumb } from '@coreui/vue'
import DefaultAside from '@/templates/containers/DefaultAside'
import DefaultHeaderDropdown from '@/templates/containers/DefaultHeaderDropdown'
import DefaultHeaderDropdownAccnt from '@/templates/containers/DefaultHeaderDropdownAccnt'
import DefaultHeaderDropdownMssgs from '@/templates/containers/DefaultHeaderDropdownMssgs'
import DefaultHeaderDropdownTasks from '@/templates/containers/DefaultHeaderDropdownTasks'
import miniToastr from 'mini-toastr'

miniToastr.init()

export default {
  name: 'SiteTemplate',
  components: {
    AsideToggler,
    AppHeader,
    AppSidebar,
    AppAside,
    TheFooter,
    Breadcrumb,
    DefaultAside,
    DefaultHeaderDropdown,
    DefaultHeaderDropdownMssgs,
    DefaultHeaderDropdownTasks,
    DefaultHeaderDropdownAccnt,
    SidebarForm,
    SidebarFooter,
    SidebarToggler,
    SidebarHeader,
    SidebarNav,
    SidebarMinimizer
  },
  created () {
    this.$store.dispatch('atualizarPermissoes')
  },
  data () {
    return {
      filtro: '',
      url: '',
      modalRestrito: false,
      modalMensagem: ''
    }
  },
  computed: {
    nav () {
      let news = []

      let home = {
        name: 'Página Inicial',
        url: '/home',
        icon: 'fa fa-home'
      }
      news.push(home)

      // let usuario = {
      //   name: 'Usuário',
      //   url: '/usuario/meucadastro',
      //   icon: 'fa fa-user'
      // }
      // news.push(usuario)

      // if (this.can('perfil.index')) {
      // let perfis = {
      //   name: 'Gerenciar Perfis',
      //   url: '/perfis',
      //   icon: 'fa fa-user-gear'
      // }
      // news.push(perfis)
      // }

      let usuarios = {
        name: 'Gerenciar Inscritos',
        url: '/usuarios',
        icon: 'fa fa-user-pen'
      }
      news.push(usuarios)

      let unidades = {
        name: 'Relatórios',
        url: '/relatorios',
        icon: 'fa fa-flag'
      }
      news.push(unidades)

      // let portaentrada = {
      //   name: 'Portas de Entrada',
      //   url: '/portaentradas',
      //   icon: 'fa fa-flag'
      // }
      // news.push(portaentrada)

      return news
    },
    computedNav () {
      return this.nav.filter((item) => console.log(item.name))
    },
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched.filter((route) => route.name || route.meta.label)
    }
  },
  methods: {
    can (permissao) {
      return this.$store.getters.validaPermissao(permissao)
    },
    pesquisar () {
      //
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      console.log(mensagem)
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  // CoreUI Icons Set
  @import '@coreui/icons/css/coreui-icons.min.css';
  /* Import Font Awesome Icons Set */
  $fa-font-path: '~font-awesome/fonts/';
  @import '~font-awesome/scss/font-awesome.scss';
  /* Import Simple Line Icons Set */
  $simple-line-font-path: '~simple-line-icons/fonts/';
  @import '~simple-line-icons/scss/simple-line-icons.scss';
  /* Import Flag Icons Set */
  @import 'flag-icon-css/css/flag-icon.min.css';
  /* Import Bootstrap Vue Styles */
  @import 'bootstrap-vue/dist/bootstrap-vue.css';
  // Import Main styles for this application
  @import '../../assets/scss/style';
</style>
<style>
 .breadcrumb a {
    color: #eb8105 !important;
  }
</style>



