<template>
  <span>
    <b-overlay :show="overlay" rounded="sm">
    <b-card no-body>
      <b-card-header>
        <b-row>
          <b-col md="9">
            <h3 tabindex="0"><strong>Gerenciar Inscritos</strong></h3>
          </b-col>
          <b-col md="3">
            <b-button block variant="dark" @click="cadastrar()">
              <i class="fa fa-plus"></i>&nbsp;&nbsp;<span>Novo</span>
            </b-button>
          </b-col>
        </b-row>
      </b-card-header>
      <b-card-body>
        <b-row>
          <b-col sm="12">
              <b-form-group tabindex="0" label="Pesquisar por nome/e-mail:">
                <b-form-input type="text" id="name" v-model="filtros.filter" placeholder="Nome/E-mail"></b-form-input>
              </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col sm="4">
            <b-form-group tabindex="0" label="Grupo de Sala:">
              <b-form-select v-model="filtros.grupo_id" class="mb-3">
                <option selected :value="null">Selecione</option>
                <option v-for="item in grupos" :key="item.id" :value="item.id"> {{item.nome}}</option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col sm="4">
            <b-form-group tabindex="0" label="Sexo:">
              <b-form-select v-model="filtros.sexo" placeholder="Sexo" class="mb-3">
                <option selected :value="null">Selecione</option>
                <option v-for="item in sexos" :key="item.id" :value="item.id"> {{item.nome}}</option>
              </b-form-select>
            </b-form-group>
          </b-col>
          <b-col sm="4">
            <b-form-group tabindex="0" label="Estado Civil:">
              <b-form-select v-model="filtros.estado_civil_id" placeholder="Estado Civil" class="mb-3">
                <option selected :value="null">Selecione</option>
                <option v-for="item in estadoscivil" :key="item.id" :value="item.id"> {{item.nome}}</option>
              </b-form-select>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col sm="2">
            <b-button block @click="limpar">
              <i class="fa fa-eraser"></i>&nbsp;&nbsp;<span>Limpar filtros</span>
            </b-button>
          </b-col>
          <b-col sm="2">
            <b-button block variant="dark" @click="pesquisar">
              <i class="fa fa-search"></i>&nbsp;&nbsp;<span>Pesquisar</span>
            </b-button>
          </b-col>
        </b-row>
      </b-card-body>
      <b-row>
        <b-col sm="12">
          <b-container fluid>
            <span>
              <table sm="12" aria-rowcount="4" id="my-table" aria-busy="false" aria-colcount="7" class="table table-responsive-sm table-responsive-md table-bordered table-striped table-sm">
                <thead class="text-center">
                  <tr>
                    <th tabindex="0" scope="col">Grupo</th>
                    <th tabindex="0" scope="col">Nome do inscrito</th>
                    <th tabindex="0" scope="col">Email</th>
                    <th tabindex="0" scope="col">Nome Lider</th>
                    <th tabindex="0" scope="col">Sexo</th>
                    <th tabindex="0" scope="col">Estado Civil</th>
                    <th tabindex="0" scope="col">Ações</th>
                  </tr>
                </thead>
                <tbody v-if="usuarios.data !== undefined" class="text-center">
                  <tr class="action" v-for="usuario in usuarios.data" :key="usuario.id">
                    <td tabindex="0">{{usuario.grupos}}</td>
                    <td tabindex="0">{{usuario.nome}}</td>
                    <td tabindex="0">{{usuario.email}}</td>
                    <td tabindex="0">{{usuario.nome_lider_gr}}</td>
                    <td tabindex="0">{{usuario.sexo === 1 ? 'Masculino' : 'Feminino'}}</td>
                    <td tabindex="0">{{usuario.estado_civil}}</td>
                    <td style="width: 1%; white-space: nowrap;">
                      <button class="btn btn-sm btn-secondary action" title="Editar" @click="editar(usuario.id)">
                        <i class="fa fa-edit"></i>
                      </button>
                      <button class="btn btn-sm btn-secondary action" title="Visualizar" @click="visualizar(usuario.id)">
                        <i class="fa fa-search"></i>
                      </button>
                      <button class="btn btn-sm btn-secondary action" title="Deletar" @click="deletar(usuario.id)">
                        <i class="fa fa-trash"></i>
                      </button>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr class="action">
                    <td tabindex="0" colspan="9">Nenhum inscrito cadastrado</td>
                  </tr>
                </tbody>
              </table>
              <b-row>
                <b-col cols="4">
                  <pagination tabindex="0" v-show="usuarios.data !== undefined" :totalPorPagina="totalPorPagina" :resource="resource" :filtros="filtros" :page="page"></pagination>
                </b-col>
              </b-row>
            </span>
          </b-container>
        </b-col>
      </b-row>
    </b-card>
    </b-overlay>
  </span>
</template>

<script>
import Vue from 'vue'
import Pagination from '@/components/shared/Pagination'
import {eventType} from '@/components/shared/eventType'
import miniToastr from 'mini-toastr'
import Multiselect from 'vue-multiselect'

miniToastr.init()

export default {
  components: {
    Pagination,
    Multiselect
  },
  created () {
    this.overlay = true
    this.$store.dispatch('getEstadosCivis')
    this.$store.dispatch('getGrupos')
  },
  data () {
    return {
      totalPorPagina: 10,
      resource: 'workshops',
      overlay: false,
      page: 1,
      sexos: [
        { id: '1', nome: 'Masculino' },
        { id: '2', nome: 'Feminino' }
      ],
      filtros: {
        filter: '',
        sexo: null,
        grupo_id: null,
        estado_civil_id: null
      }
    }
  },
  computed: {
    usuarios () {
      if (this.$store.state.pagination.getList.data !== undefined) {
        this.overlay = false
        if (this.$store.state.pagination.getList.data.length !== 0) {
          return this.$store.state.pagination.getList
        } else {
          return false
        }
      }
      return this.$store.state.pagination.getList
    },
    grupos () {
      return this.$store.state.grupos.listaGrupo
    },
    estadoscivil () {
      return this.$store.state.estadosCivis.listaEstadosCivi
    }
  },
  methods: {
    can (permissao) {
      return this.$store.getters.validaPermissao(permissao)
    },
    setarFiltros (filtros) {
      eventType.$emit('app', filtros)
    },
    pesquisar () {
      this.overlay = true
      this.$store.dispatch('getRegistrosSearch', {resource: this.resource, limit: this.totalPorPagina, filtros: this.filtros, page: this.page})
      this.setarFiltros(this.filtros)
    },
    cadastrar () {
      this.$router.push('/usuario/cadastrar')
    },
    editar (id) {
      this.$router.push('/usuario/' + id + '/editar')
    },
    visualizar (id) {
      this.$router.push('/usuario/' + id + '/visualizar')
    },
    navigate (route) {
      this.$router.push(route)
    },
    limpar () {
      this.filtros = {
        filter: '',
        sexo: null,
        grupo_id: null,
        estado_civil_id: null
      }

      this.pesquisar()
    },
    deletar (id) {
      var msg = 'Confirma a desativação do registro selecionado?'
      this.$bvModal.msgBoxConfirm(msg, {
        okTitle: 'Confirmar',
        cancelTitle: 'Cancelar'
      }).then(valor => {
        if (valor) {
          let url = 'api/workshops/' + id

          Vue.http.delete(url).then(response => {
            if (response.body.status) {
              this.sucesso(response.body.message)
              this.overlay = true
              this.$store.dispatch('getRegistrosSearch', {resource: this.resource, limit: this.totalPorPagina, filtros: this.filtros, page: this.page})
            }
          }).catch(erro => {
            if (!erro.body.status) {
              this.erro(erro.body.message)
              this.overlay = true
              this.$store.dispatch('getRegistrosSearch', {resource: this.resource, limit: this.totalPorPagina, filtros: this.filtros, page: this.page})
            }
          })
        }
      }).catch(erro => {
        console.log(erro)
      })
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style>
    .multiselect.invalid .multiselect__tags,
    .multiselect.invalid .multiselect__tags span,
    .multiselect.invalid .multiselect__tags input {
        background:red;
    }

td {
  max-width: 15ch;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



