import Vue from 'vue'

const state = {
  listaUnidade: []
}

const mutations = {
  setUnidades (state, data) {
    state.listaUnidade = data
  }
}

const getters = {
}

const actions = {
  getUnidades (context) {
    Vue.http.get('api/unidades?ativo=1').then(response => {
      context.commit('setUnidades', response.body.data.data)
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



