import Vue from 'vue'

const state = {
  listaGrupo: []
}

const mutations = {
  setGrupos (state, data) {
    state.listaGrupo = data
  }
}

const getters = {
}

const actions = {
  getGrupos (context) {
    Vue.http.get('api/grupos').then(response => {
      context.commit('setGrupos', response.body.data)
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



