<template>
  <auth-template>
    <b-col md="8">
      <b-card-group>
        <b-card no-body class="p-4">
          <b-card-body>
            <b-form>
              <b-row>
                <b-col sm="12">
                  <b-form-group tabindex="0" label="Digite o login: *">
                    <b-form-input type="text" id="ds_login" v-model="ds_login" placeholder="Login">
                    </b-form-input>
                  </b-form-group>
                </b-col>
                <b-col sm="12">
                  <b-form-group tabindex="0" label="Senha: *">
                    <b-form-input type="password" id="password" v-model="senha" placeholder="Senha">
                    </b-form-input>
                  </b-form-group>
                </b-col>
              </b-row>
              <div style="margin-top: 3rem; margin-left: 2rem; margin-bottom: 3rem;">
                <a @click="login">
                  <b-overlay :show="busy" rounded opacity="0.6" style="width: 80%" spinner-small spinner-variant="dark"
                    class="d-inline-block" @hidden="onHidden">
                    <b-button :disabled="busy" style="width: 80%" ref="button" variant="outline-dark">Entrar</b-button>
                  </b-overlay>
                </a>
              </div>
            </b-form>
          </b-card-body>
        </b-card>
        <b-card no-body class="text-white bg-dark py-5 d-md-down-none" style="width:44%">
          <b-card-body class="text-center">
            <div>
              <img :src="require(`@/assets/logo.png`)" class="img-logo" alt="Chama Church" srcset="Chama Chruch">
              <br>
              <h2><strong>Painel Workshop</strong></h2>
            </div>
          </b-card-body>
        </b-card>
      </b-card-group>
    </b-col>
  </auth-template>
</template>

<script>
import Vue from 'vue'
import AuthTemplate from '@/templates/auth/AuthTemplate'

export default {
  name: 'Login',
  components: {
    AuthTemplate
  },
  data () {
    return {
      busy: false,
      ds_login: null,
      senha: null
    }
  },
  methods: {
    navigate (route) {
      this.$router.push(route)
    },
    login () {
      this.busy = true
      // eslint-disable-next-line standard/object-curly-even-spacing
      let data = { ds_login: this.ds_login, password: this.senha }
      Vue.http.post(('api/auth/login'), data).then(response => {
        const data = response.data
        console.log(data)
        this.$store.dispatch('login', data).then(() => {
          this.$router.push('/home')
        }).catch(e => {
          console.log('Servidor está fora! ' + e)
          this.busy = false
        })
      }).catch(e => {
        this.busy = false
        const { body } = e
        this.erro(body ? body.message : 'Sistema fora de serviço')
      })
    },
    onHidden () {
      // Return focus to the button once hidden
      this.$refs.button.focus()
    },
    erro (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>
<style>
.img-logo {
  width: 150px;
  height: 150px;
  margin-bottom: 1rem;
}
</style>



