import Vue from 'vue'
import JwtToken from '@/services/jwt-token'
import miniToastr from 'mini-toastr'

miniToastr.init()

export default {

  checkAuth: function () {
    Vue.http.interceptors.push((request, next) => {
      request.headers.set('Authorization', JwtToken.getAuthorizationHeader())
      next(function (response) {
        if (response.body.status === false && request.url !== 'api/auth/login') {
          miniToastr.setIcon('error', 'i', { 'class': 'fa fa-check' })
          miniToastr.error(response.body.msg, 'Alerta!', 7000)
        }
      })
    })
  },
  refreshAuth: function () {
    Vue.http.interceptors.push((request, next) => {
      if (request.url !== 'api/auth/login') {
        next(response => {
          if (response.status === 401) {
            // token expirado
            return JwtToken.refreshToken()
              .then((res) => {
                if (res) { return Vue.http(request) }
                window.location.replace('/login')
              }).catch(() => {
                window.location.replace('/login')
              })
          } else if (response.status === 500) { window.location.replace('/login') }
        })
      }
    })
  },
  refreshNotifications: function () {
    Vue.http.interceptors.push((request, next) => {
      request.headers.set('Authorization', JwtToken.getAuthorizationHeader())
      next(function (response) {
      })
    })
  },
  logout () {
    sessionStorage.clear()
    miniToastr.error('Token da sessão expirado.', 'Alerta!', 4000)
    setTimeout(() => {
      Vue.http.get('api/auth/logout').then(() => window.location.replace('/login'))
    }, 2000)
  }
}



