import Pagination from '@/vuex/modules/pagination'
import Usuario from '@/vuex/modules/usuario'
import Perfil from '@/vuex/modules/perfil'
import Estado from '@/vuex/modules/estado'
import Cidade from '@/vuex/modules/cidade'
import Permissao from '@/vuex/modules/permissao'
import TokenEmail from '@/vuex/modules/token'
import Unidade from '@/vuex/modules/unidade'
import grupos from '@/vuex/modules/grupos'
import estadosCivis from '@/vuex/modules/estados_civis'
import perfilUser from '@/vuex/modules/perfiluser'

export default {
  modules: {
    usuario: Usuario,
    estado: Estado,
    cidade: Cidade,
    token: TokenEmail,
    pagination: Pagination,
    permissao: Permissao,
    perfil: Perfil,
    unidade: Unidade,
    perfiluser: perfilUser,
    grupos,
    estadosCivis
  }
}



