<template>
  <b-col md="12" sm="12">
    <b-card no-body class="mx-4">
      <b-card-body class="p-12">
        <b-form v-on:submit.prevent="">
          <h1 tabindex="0" v-if="!form.id"><strong>Cadastrar Novo Inscrito</strong></h1>
          <h1 tabindex="0" v-if="form.id && !visualizar"><strong>Editar Inscrito</strong></h1>
          <h1 tabindex="0" v-if="visualizar"><strong>Visualizar Inscrito</strong></h1>
          <b-row>
            <b-col cols="8">
              <b-form-group tabindex="0" label="Nome Completo: *">
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text><i class="icon-user"></i></b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="text" class="form-control" placeholder="Nome" autocomplete="nome"
                    v-model.trim="$v.form.nome.$model" :disabled="visualizar"  autofocus/>
                  <b-form-invalid-feedback id="input1LiveFeedback3" :state="chkState('nome')">Nome é obrigatório.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col sm="4">
              <b-form-group tabindex="0" label="Situação: *">
                <b-form-radio-group v-model="$v.form.ativo.$model" :options="status" required :disabled="visualizar" />
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <b-form-group tabindex="0" label="CPF: *">
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text>
                      <i class='fa fa-address-card'></i>
                    </b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input v-mask="'###.###.###-##'" type="text" class="form-control" placeholder="CPF"
                    autocomplete="cpf" v-model="$v.form.cpf.$model" :disabled="visualizar" />
                  <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('cpf')">CPF é obrigatório.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Data de Nascimento: *">
                <b-input-group class="mb-3">
                  <b-form-input type="date" class="form-control" placeholder="DD/MM/AAAA"  autocomplete="data"
                    v-model="$v.form.data_nascimento.$model"
                    :disabled="visualizar" />
                  <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('data_nascimento')">Data de nascimento é obrigatório.
                  </b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col sm="4">
              <b-form-group tabindex="0" label="Sexo: *">
                <b-form-radio-group v-model="$v.form.sexo.$model" :options="sexo" required :disabled="visualizar" />
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Profissão: *">
                <b-form-select v-model="form.profissao" placeholder="Profissão de Usuário" class="mb-3"
                  :disabled="visualizar">
                  <option selected :value="null">Selecione</option>
                  <option v-for="item in profissoes" :key="item.id" :value="item.id"> {{ item.nome }}</option>
                </b-form-select>
                <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('profissao')">Profissão é obrigatório.
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Data de Admissão: *">
                <b-input-group class="mb-3">
                  <b-form-input type="date" class="form-control" placeholder="DD/MM/AAAA" autocomplete="data"
                    v-model="$v.form.data_admissao.$model" :disabled="visualizar" />
                  <b-form-invalid-feedback :state="chkState('data_admissao')" id="input1LiveFeedback1">Data de admissão é obrigatório.
                  </b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Estado: *">
                <b-form-select v-model="$v.form.estado_id.$model" class="mb-3" :disabled="visualizar">
                  <option selected :value="null"> Selecione </option>
                  <option v-for="estado in estados" :value="estado.id" :key="estado.id">{{ estado.estado }} - {{
                      estado.sigla
                  }} </option>
                </b-form-select>
                <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('estado_id')">Estado é obrigatório.
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Município: *">
                <b-form-select v-model="$v.form.cidade_id.$model" class="mb-3" :disabled="visualizar">
                  <option selected :value="null"> Selecione </option>
                  <option v-for="cidade in cidades" :value="cidade.id" :key="cidade.id">{{ cidade.cidade }}</option>
                </b-form-select>
                <b-form-invalid-feedback id="input1LiveFeedback1" :state="chkState('cidade_id')">Município é obrigatório.
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="6">
              <b-form-group tabindex="0" label="E-mail: *">
                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text>@</b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="text" class="form-control" placeholder="E-mail" autocomplete="email"
                    v-model.trim="$v.form.email.$model" :disabled="visualizar" />
                  <b-form-invalid-feedback id="input1LiveFeedback7" :state="chkState('email')">Este é um campo obrigatório, e deve ser um endereço
                    de e-mail válido.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="4">
              <b-form-group tabindex="0" label="Telefone: ">
                <b-input-group class="mb-3">
                  <b-form-input type="text" class="form-control" placeholder="Telefone" autocomplete="nome"
                    v-mask="'(##) #####-####'" v-model="$v.form.telefone.$model" :disabled="visualizar" />
                  <b-form-invalid-feedback id="input1LiveFeedback3" :state="chkState('telefone')">Telefone é
                    obrigatório.</b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col sm="4">
              <b-form-group tabindex="0" label="Celular: *">
                <b-input-group class="mb-3">
                  <b-form-input type="text" class="form-control" :disabled="visualizar" placeholder="Celular" autocomplete="nome"
                    v-mask="'(##) #####-####'" v-model="$v.form.celular.$model"  />
                  <b-form-invalid-feedback id="input1LiveFeedback3" :state="chkState('celular')">Celular é obrigatório.
                  </b-form-invalid-feedback>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" class="mb-3">
              <h6>Adicione perfil e vincule uma ou mais unidades:</h6>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Perfil: *">
                <b-form-select v-model="form.perfil_id" placeholder="Tipo de Usuário" class="mb-3"
                  :disabled="visualizar">
                  <option selected :value="null">Selecione</option>
                  <option v-for="item in perfis" :key="item.id" :value="item.id"> {{ item.nome }}</option>
                </b-form-select>
              </b-form-group>
            </b-col>
            <b-col cols="4">
              <b-form-group tabindex="0" label="Unidade: *">
                <b-form-select v-model="form.unidade_id" placeholder="Unidade do Usuário" class="mb-3"
                  :disabled="visualizar">
                  <option selected :value="null">Selecione</option>
                  <option v-for="item in unidades" :key="item.id" :value="item.id"> {{ item.nome }}</option>
                </b-form-select>
              </b-form-group>
            </b-col>
            <b-col md="2" key="new_p">
              <b-button block variant="primary" class="mt-4" :disabled="visualizar" v-on:click="addPerfil">
                <i class="fa fa-plus"></i>&nbsp;
                <span>Adicionar</span>
              </b-button>
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="10">
              <b-container fluid>
                <span>
                  <table aria-rowcount="4" id="my-table" aria-busy="false" aria-colcount="7"
                    class="table table-responsive-sm table-bordered table-striped table-sm">
                    <thead>
                      <tr>
                        <th tabindex="0" scope="col">Perfil</th>
                        <th tabindex="0" scope="col">Unidade</th>
                        <th tabindex="0" scope="col">UF</th>
                        <th v-if="!visualizar" tabindex="0" scope="col">Ações</th>
                      </tr>
                    </thead>
                    <tbody v-if="arrayPerfis !== undefined">
                      <tr class="action" v-for="(item , index) in arrayPerfis" :key="item.id">
                        <td tabindex="0">{{ item.perfil }}</td>
                        <td tabindex="0">{{ item.unidade }}</td>
                        <td tabindex="0">{{ item.uf}}</td>
                        <td v-if="!visualizar" style="width: 1%; white-space: nowrap;">
                          <button class="btn btn-sm btn-secondary" title="Remover" v-on:click="rmPerfil(index)">
                            <i class="fa fa-trash"></i>
                          </button>
                        </td>
                      </tr>
                    </tbody>
                    <tbody v-else>
                      <tr class="action">
                        <td tabindex="0" colspan="9">Nenhum Perfil Cadastrado</td>
                      </tr>
                    </tbody>
                  </table>
                </span>
              </b-container>
            </b-col>
          </b-row>
          <!-- <b-row>
            <b-col md="6" sm="12" cols="12">
              <label tabindex="0" v-if="!visualizar">Foto:</label>
              <span class="icon-info" v-b-popover.hover.top="'JPG, JPEG, PNG'" title="Extensões aceitas"
                v-if="!visualizar"></span>
              <b-form-group label-for="nome-input" v-if="!visualizar">
                <b-form-file class="fileInput" accept="image/png, image/jpeg, image/jpg" v-on:change="uploadFoto"
                  v-model="form.foto" browse-text="Selecione" placeholder="Selecione uma foto"
                  :state="chkState('foto')"></b-form-file>
                <b-form-invalid-feedback :state="chkState('foto')" id="input1LiveFeedback3">Assinatura é
                  obrigatória.
                </b-form-invalid-feedback>
                <img :src="form.foto" v-if="form.foto" style="width: 50%" />
              </b-form-group>
            </b-col>
          </b-row> -->
        </b-form>
      </b-card-body>
      <b-card-footer class="p-12">
        <b-row>
          <b-col v-if="!loading && !visualizar" md="3" key="new_p">
            <b-button block variant="primary" :disabled="disable_buttons" v-on:click="onSubmit">
              <i class="fa fa-plus"></i>&nbsp;
              <span>Salvar</span>
            </b-button>
          </b-col>
          <b-col v-else-if="!visualizar" md="3">
            <b-button block variant="primary" :disabled="true" v-on:click="onSubmit">
              <b-spinner small type="grow"></b-spinner>Salvando...
            </b-button>
          </b-col>
          <b-col md="3">
            <b-button block variant="secondary" :disabled="disable_buttons" @click="voltar">
              <i class="fa fa-mail-reply-all"></i>&nbsp;&nbsp;<span>Voltar</span>
            </b-button>
          </b-col>
        </b-row>
      </b-card-footer>
    </b-card>
  </b-col>
</template>

<script>
import Vue from 'vue'
import VueSelect from 'vue-select'
import { validationMixin } from 'vuelidate'
import { required, minLength, email } from 'vuelidate/lib/validators'
import miniToastr from 'mini-toastr'
import Multiselect from 'vue-multiselect'

miniToastr.init()

export default {
  name: 'Editar',
  components: {
    VueSelect,
    Multiselect
  },
  mixins: [validationMixin],
  validations: {
    form: {
      cpf: {
        required,
        minLength: minLength(14)
      },
      nome: {
        required
      },
      email: {
        required,
        email
      },
      data_nascimento: {
        required
      },
      profissao: {
        required
      },
      data_admissao: {
        required
      },
      estado_id: {
        required
      },
      cidade_id: {
        required
      },
      telefone: {
        // required
      },
      celular: {
        required
      },
      ativo: {
        // required
      },
      sexo: {
        // required
      },
      foto: {}
    }
  },
  created () {
    this.$store.dispatch('getPerfisListaCombo')
    this.$store.dispatch('getUnidades')
    this.$store.dispatch('buscarEstados')
  },
  data () {
    return {
      loading: false,
      form: {
        id: '',
        cpf: '',
        nome: '',
        email: '',
        perfil_id: null,
        unidade_id: null,
        // foto: null,
        data_nascimento: null,
        sexo: false,
        profissao: null,
        data_admissao: null,
        estado_id: null,
        cidade_id: null,
        telefone: null,
        celular: null,
        ativo: true
      },
      visualizar: false,
      disable_buttons: false,
      salvarForm: false,
      arrayPerfis: [],
      alterado: false,
      status: [
        { value: true, text: 'Ativo' },
        { value: false, text: 'Inativo' }
      ],
      sexo: [
        { value: true, text: 'Masculino' },
        { value: false, text: 'Feminino' }
      ],
      profissoes: [
        { id: 1, nome: 'Gestor' },
        { id: 2, nome: 'Coordenador Técnico' }
      ]
    }
  },
  computed: {
    perfis () {
      return this.$store.state.perfil.lista
    },
    isValid () {
      return !this.$v.form.$anyError
    },
    estados () {
      return this.$store.state.estado.listaEstados
    },
    isValidSelection () {
      return this.selected !== '' && this.selected !== 'forbidden value'
    },
    unidades () {
      return this.$store.state.unidade.listaUnidade
    },
    cidades () {
      return this.$store.state.cidade.listaCidadesPorEstado
    }
  },
  watch: {
    '$v.form.estado_id.$model': function (id) {
      this.$store.dispatch('buscarCidadesPorEstado', id)
    }
  },
  mounted () {
    if (this.$route.params.id !== undefined) {
      this.form.id = this.$route.params.id
      this.loadUser(this.$route.params.id)
    }

    if (this.$route.params.id !== undefined && this.$route.params.visualizar !== undefined) {
      this.visualizar = true
      this.loadUser(this.$route.params.id)
    }
  },
  methods: {
    uploadFoto (evento) {
      let arquivo = evento.target.files || evento.dataTransfer.files

      if (!arquivo.length) {
        return false
      }

      let reader = new FileReader()

      reader.onload = (evento) => {
        this.form.foto = evento.target.result
      }

      reader.readAsDataURL(arquivo[0])
    },
    abrirDocumento (path) {
      window.open(path, '_blank')
    },
    addPerfil () {
      let unidade = (this.unidades.find((e) => e.id === this.form.unidade_id)).nome
      let perfil = (this.perfis.find((e) => e.id === this.form.perfil_id)).nome
      let uf = (this.unidades.find((e) => e.id === this.form.unidade_id)).estado.estado + ' - ' + (this.unidades.find((e) => e.id === this.form.unidade_id)).estado.sigla
      let data = {
        perfil_id: this.form.perfil_id,
        perfil,
        unidade_id: this.form.unidade_id,
        unidade,
        uf
      }
      this.arrayPerfis.push(data)
      this.alterado = true
    },
    rmPerfil (id) {
      this.arrayPerfis.splice(+(id), 1)
      this.alterado = true
      this.sucesso('Unidade removida com sucesso!')
    },
    admEstado (id) {
      Vue.http.get('api/usuario/' + id).then(response => {
        let data = response.body.data[0]
        if (data.perfil === 'Administrador Estado') {
          this.admEstadoBool = true
        } else {
          this.admEstadoBool = false
        }
      })
    },
    can (permissao) {
      return this.$store.getters.validaPermissao(permissao)
    },
    loadUser (id) {
      Vue.http.get('api/user/' + id).then(response => {
        let data = response.body.data
        this.form.id = data.id
        this.form.cpf = data.cpf
        this.form.nome = data.nome
        this.form.email = data.email
        this.form.ativo = data.ativo
        this.form.data_nascimento = data.data_nascimento
        this.form.sexo = data.sexo
        this.form.profissao = data.profissao
        this.form.data_admissao = data.data_admissao
        this.form.estado_id = data.estado_id
        this.form.cidade_id = data.cidade_id
        this.form.telefone = data.telefone
        this.form.celular = data.celular

        if (data.perfis_user) {
          this.arrayPerfis = []
          data.perfis_user.map(p => {
            let unidade = (this.unidades.find((e) => e.id === p.unidade_id)).nome
            let perfil = (this.perfis.find((e) => e.id === p.perfil_id)).nome
            let uf = (this.unidades.find((e) => e.id === p.unidade_id)).estado.estado + ' - ' + (this.unidades.find((e) => e.id === p.unidade_id)).estado.sigla
            let item = {
              perfil_id: p.perfil_id,
              perfil,
              unidade_id: p.unidade_id,
              unidade,
              uf
            }
            this.arrayPerfis.push(item)
          })
        }

        // this.form.foto = data.foto
      })
    },
    salvar () {
      let form = this.$v.form
      let perfis = []

      this.arrayPerfis.map((e) => {
        perfis.push({id: e.perfil_id, unidade_id: e.unidade_id})
      })
      let data = {
        nome: form.nome.$model,
        cpf: form.cpf.$model.replace(/[^\d]+/g, ''),
        email: form.email.$model,
        perfis: JSON.stringify(perfis),
        ativo: this.form.ativo,
        // foto: form.foto.$model,
        data_nascimento: form.data_nascimento.$model,
        sexo: form.sexo.$model,
        profissao: form.profissao.$model,
        data_admissao: form.data_admissao.$model,
        estado_id: form.estado_id.$model,
        cidade_id: form.cidade_id.$model,
        telefone: form.telefone.$model ? form.telefone.$model.replace(/[^0-9]+/g, '') : '',
        celular: form.celular.$model ? form.celular.$model.replace(/[^0-9]+/g, '') : '',
        alterado: this.alterado
      }

      this.loading = true

      if (this.form.id) {
        Vue.http.put('api/user/' + this.form.id, data).then(response => {
          if (response.body.success) {
            this.sucesso(response.body.msg)
            let t = this
            setTimeout(function () {
              t.navigate('/usuarios')
            }, 3000)
          } else {
            this.erro(response.body.msg)
          }

          this.loading = false
        }).catch(e => {
          this.loading = false
          if (e.data.data) { this.erro(e.data.data[0]) } else { this.erro('Falha ao editar usuário!') }
        })
      } else {
        Vue.http.post('api/user', data).then(response => {
          if (response.body.success) {
            this.sucesso(response.body.msg)
            let t = this
            setTimeout(function () {
              t.navigate('/usuarios')
            }, 3000)
          } else {
            this.erro(response.body.msg)
          }
          this.loading = false
        }).catch(e => {
          this.loading = false
          if (e.data.data) { this.erro(e.data.data[0]) } else { this.erro('Falha ao cadastrar usuário!') }
        })
      }
    },
    confirmar () {
      this.$bvModal.msgBoxConfirm('Tem certeza que deseja continuar?', {
        okTitle: 'Confirmar',
        cancelTitle: 'Cancelar'
      }).then(valor => {
        if (valor) {
          this.salvar()
        }
      }).catch(erro => {
        console.log(erro)
      })
    },
    onSubmit () {
      this.salvarForm = true
      if (this.validate() && this.validateCep() && this.dataValidate()) {
        this.confirmar()
      }
    },
    chkState (val) {
      const field = this.$v.form[val]
      if (!field.$model && !this.salvarForm) {
        return null
      }

      return !field.$dirty || !field.$invalid
    },
    findFirstError (component = this) {
      if (component.state === false) {
        if (component.$refs.input) {
          component.$refs.input.focus()
          return true
        }
        if (component.$refs.check) {
          component.$refs.check.focus()
          return true
        }
      }
      let focused = false
      component.$children.some((child) => {
        focused = this.findFirstError(child)
        return focused
      })

      return focused
    },
    validate () {
      this.$v.$touch()
      this.$nextTick(() => this.findFirstError())
      return this.isValid
    },
    validateCep () {
      if (this.form.cep === '') {
        return false
      }
      if (this.arrayPerfis.length < 1) {
        this.erro('Perfil e Unidade obrigatórios!')
        return false
      }
      return true
    },
    navigate (route) {
      this.$router.push(route)
    },
    voltar () {
      if (this.visualizar) { this.$router.push('/usuarios') }
      this.$bvModal.msgBoxConfirm('Deseja retornar para tela de pesquisa? É possível que dados informados não sejam salvos.', {
        okTitle: 'Confirmar',
        cancelTitle: 'Cancelar'
      }).then(valor => {
        if (valor) {
          this.$router.push('/usuarios')
        }
      })
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    },
    dataValidate () {
      let resultado = this.form.data_nascimento.split('-')
      let ano1 = resultado[0]

      resultado = this.form.data_admissao.split('-')
      let ano2 = resultado[0]

      const date = new Date()
      const currentYear = date.getFullYear()

      if (ano1.length !== 4 || (Number(currentYear) - Number(ano1) < 18)) {
        this.erro('Data de nascimento inválida!')
        return false
      }

      if (ano2.length !== 4 || (Number(currentYear) - Number(ano2) < 0)) {
        this.erro('Data de admissão inválida!')
        return false
      }
      return true
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css">
</style>



