<template>
  <span>
    <b-overlay :show="overlay" rounded="sm">
    <b-card no-body>
      <b-card-header>
        <b-row>
          <b-col md="9">
            <h3 tabindex="0"><strong>Relatórios </strong></h3>
          </b-col>
        </b-row>
      </b-card-header>
      <b-card-body>
        <b-row>
          <b-col offset-sm="4" sm="4">
            <b-card bg-variant="dark" header="Total de Inscritos" text-variant="white" class="text-center">
              <b-card-text>{{ total }}</b-card-text>
            </b-card>
          </b-col>
        </b-row>
        <b-row>
          <b-col md="12" class="text-center">
            <h3 tabindex="0"><strong>Inscrito por Grupos</strong></h3>
          </b-col>
          <b-col md="1" class="text-center">
          </b-col>
          <b-col sm="2" v-for="grupo in grupos" :key="grupo.id">
            <b-card bg-variant="dark" :header="grupo.nome" text-variant="white" class="text-center">
              <b-card-text>{{gruposResponse[grupo.id]}}</b-card-text>
            </b-card>
          </b-col>
        </b-row>
        <b-row>
          <b-col md="12" class="text-center">
            <h3 tabindex="0"><strong>Inscrito por Estado Civil</strong></h3>
          </b-col>
          <b-col sm="3" v-for="estado in estadoscivil" :key="estado.id">
            <b-card bg-variant="dark" :header="estado.nome" text-variant="white" class="text-center">
              <b-card-text>{{estadosResponse[estado.id]}}</b-card-text>
            </b-card>
          </b-col>
        </b-row>
        <b-row>
          <b-col md="12" class="text-center">
            <h3 tabindex="0"><strong>Inscrito por Sexo</strong></h3>
          </b-col>
          <b-col offset-sm="3" sm="3">
            <b-card bg-variant="dark" header="Masculino" text-variant="white" class="text-center">
              <b-card-text>{{ masculino }}</b-card-text>
            </b-card>
          </b-col>
          <b-col sm="3">
            <b-card bg-variant="dark" header="Feminino" text-variant="white" class="text-center">
              <b-card-text>{{ feminino}}</b-card-text>
            </b-card>
          </b-col>
        </b-row>
      </b-card-body>
    </b-card>
  </b-overlay>
  </span>
</template>

<script>
import Vue from 'vue'
import miniToastr from 'mini-toastr'

miniToastr.init()

export default {
  created () {
    // if (!this.can('servico.index')) {
    //   this.$router.push('/home')
    // }
    this.loadingData()
    this.$store.dispatch('getEstadosCivis')
    this.$store.dispatch('getGrupos')
  },
  data () {
    return {
      overlay: false,
      gruposResponse: [],
      estadosResponse: [],
      total: 0,
      feminino: 0,
      masculino: 0
    }
  },
  computed: {
    grupos () {
      return this.$store.state.grupos.listaGrupo
    },
    estadoscivil () {
      return this.$store.state.estadosCivis.listaEstadosCivi
    }
  },
  methods: {
    can (permissao) {
      return this.$store.getters.validaPermissao(permissao)
    },
    loadingData () {
      this.overlay = true
      Vue.http.get('api/relatorios').then(response => {
        if (response.body.status) {
          const {total, sexos, grupos, estados} = response.body.data
          this.total = total
          this.masculino = sexos.masculino
          this.feminino = sexos.feminino
          let grupTemp = Object.values(grupos)
          let estadosTemp = Object.values(estados)
          grupTemp.forEach((value, index) => {
            this.gruposResponse[(index + 1)] = value
          })
          estadosTemp.forEach((value, index) => {
            this.estadosResponse[(index + 1)] = value
          })
        }
        this.overlay = false
      }).catch(erro => {
        this.overlay = false
        if (!erro.body.status) {
          this.erro(erro.body.msg)
        }
      })
    },
    navigate (route) {
      this.$router.push(route)
    },
    sucesso (mensagem) {
      this.$bvToast.toast(mensagem, {
        title: 'Sucesso',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'info'
      })
    },
    erro (mensagem) {
      console.log(mensagem)
      this.$bvToast.toast(mensagem, {
        title: 'Erro',
        autoHideDelay: 10000,
        appendToast: true,
        variant: 'danger'
      })
    }
  }
}
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css">

</style>
<style>
.card-text {
  font-size: 2rem;
}
</style>



