<template>
<span>
    <h3>Seja bem-vindo(a) {{usuario}}!</h3>
</span>
</template>
<script>
import miniToastr from 'mini-toastr'
import SessionStorage from '@/services/session-storage'

miniToastr.init()

export default {
  name: 'HelloWorld',
  data () {
    return {
      usuario: SessionStorage.get('nome')
    }
  }
}
</script>



