<template>
  <AppHeaderDropdown right no-caret>
    <template slot="header" v-if="avatar != null">
      <img :src="avatar"
        width="35"
        height="35"
        class="img-avatar"
        alt="" />
    </template>\
    <template slot="dropdown">
      <b-dropdown-header tag="div" class="text-center"><strong>Conta</strong></b-dropdown-header>
      <!-- <b-dropdown-item v-on:click="redirect()"><i class="fa fa-user" /> Meu Perfil</b-dropdown-item>
      <b-dropdown-divider /> -->
      <b-dropdown-item v-on:click="logout"><i class="fa fa-lock" /> Sair</b-dropdown-item>
    </template>
  </AppHeaderDropdown>
</template>

<script>
import { HeaderDropdown as AppHeaderDropdown } from '@coreui/vue'
import SessionStorage from '@/services/session-storage'
import Vue from 'vue'
export default {
  name: 'DefaultHeaderDropdownAccnt',
  components: {
    AppHeaderDropdown
  },
  created () {
    this.nome = SessionStorage.get('nome')
    this.avatar = SessionStorage.get('avatar') ? SessionStorage.get('avatar') : 'https://cdn-icons-png.flaticon.com/512/149/149071.png'
  },
  data: () => {
    return {
      itemsCount: 42,
      url: null,
      nome: null,
      avatar: null
    }
  },
  computed: {
    usuario () {
      return this.$store.state.usuario.usuarioEditar
    }
  },
  methods: {
    redirect () {
      this.$router.push(this.url)
    },
    logout () {
      sessionStorage.clear()
      Vue.http.get('api/auth/logout').then(() => this.$router.push('/login'))
    },
    meuPerfil () {
      this.$router.push('/perfil')
    }
  }
}
</script>



