import Vue from 'vue'

const state = {
  listaEstadosCivi: []
}

const mutations = {
  setEstadosCivis (state, data) {
    state.listaEstadosCivi = data
  }
}

const getters = {
}

const actions = {
  getEstadosCivis (context) {
    Vue.http.get('api/estados-civis').then(response => {
      context.commit('setEstadosCivis', response.body.data)
    })
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



